@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
* {
    margin: 0;
    padding: 0;
}

.sidebar {
    width: 268px;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    background-color: white;
}

.logoContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 80px;
}

.logo {
    margin-top: 20px;
    width: 50%;
}

.sidebarElement {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    padding: 20px;
    padding-left: 20%;
    cursor: pointer;
}

.imgContainer {
    display: flex;
    justify-content: center;
    width: 20px;
}

.sidebarElement img {
    height: 1.1rem;
}

.sidebarElement h4 {
    position: fixed;
    left: 100px;
    font-size: 1rem;
    font-weight: 500;
}

.selected {
    background-color: #f7258428;
    position: relative;
}

.selected .marker {
    width: 5px;
    height: 100%;
    position: absolute;
    background-color: #f72585;
    border-radius: 0 100px 100px 0;
    right: -5px;
}

.connection {
    cursor: auto;
    position: absolute;
    left: 0;
    bottom: 20px;
}

.errorLeiste {
    position: fixed;
    margin-left: 268px;
    bottom: 0;
    left: 0;
    display: flex;
    background-color: rgba(27, 27, 27, 0.9);
    justify-content: center;
    width: calc(100% - 268px);
    z-index: 1;
    color: white;
    flex-direction: column;
    align-items: center;
    padding-bottom: 1rem;
}

.fadeIn {
    -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
}

.fadeOut {
    opacity: 0;
    -webkit-animation: fadeOut 1s ;
            animation: fadeOut 1s ;
    transform: translateY(100%);
}

.errorLeiste h1 {
    margin: 1rem;
}

.downloadArea {
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(100%);
    }

    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(100%);
    }

    to {
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut {
    from {
        opacity: 1;
        transform: translateY(-0%);
    }

    to {
        opacity: 0;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
        transform: translateY(-0%);
    }

    to {
        opacity: 0;
    }
}

h1 {
    font-size: 2rem;
    font-weight: 600;
    margin: 50px 0;
}

h2 {
    font-size: 1.5rem;
    font-weight: 600;
    margin: 50px 0;
}

.presentation {
    width: calc(100% - 3rem);
    height: 130px;
    min-width: 560px;
}

.presentation h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    font-weight: 600;
}

.presentation p {
    font-size: 1.3rem;
    font-weight: 400;
}

.downloadContainer {
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
}

.downloadContainer a {
    display: block;
}

.downloadContainer p {
    color: #9c9c9c;
    font-weight: 500;
    font-size: 0.8rem;
}

.left {
    position: absolute;
    right: 20px;
    bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    grid-gap: 20px;
    gap: 20px;
}

.left img {
    margin-right: 30px;
}

.exploreContent {
    display: flex;
    justify-content: flex-start;
    grid-gap: 20px;
    gap: 20px;
    width: calc(50% - 25px - 3rem);
}

.exploreContent img {
    height: 100%;
}

.exploreContent a {
    position: absolute;
    right: 20px;
    bottom: 20px;
}

.exploreContent p {
    height: 80px;
}

.exploreContent h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.description {
    width: 70%;
}

.exploreContainers,
.informationContainers {
    display: flex;
    flex-wrap: wrap;
    grid-gap: 50px;
    gap: 50px;
}

.input {
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
}

.input a {
    position: static;
}

.input input {
    border-radius: 5px;
    border: solid 3px black;
    padding: 0.5rem;
    font-size: 1rem;
    font-weight: 500;
    width: calc(150px - 1rem);
    margin-bottom: 1rem;
}

.informationsContent {
    display: flex;
    justify-content: flex-start;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    grid-gap: 20px;
    gap: 20px;
    width: calc((100% - 200px - 3rem) / 3);
}

.informationsContent img {
    height: 95px;
}

.informationsContent p {
    height: 40px;
    overflow-y: scroll;
    word-break: break-all;
}

.oid {
    position: absolute;
    bottom: 0;
    left: 0;
    color: #9c9c9c;
    font-size: 0.7rem;
}

.informationsContent h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.description {
    height: 95px;
    position: relative;
}

.mainInput {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    width: calc(100% - 100px - 3rem - ((100% - 200px - 3rem) / 3));
    margin-top: 50px;
    justify-content: space-between;
}

.inputContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.mainInput h3 {
    font-size: 1.5rem;
    font-weight: 600;
}

.inputs {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.iconButtons {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.iconButtons img {
    width: 100%;
}

.inputs input {
    border-radius: 5px;
    border: solid 3px black;
    padding: 0.5rem;
    font-size: 1rem;
    font-weight: 500;
    width: 300px;
    margin-bottom: 1rem;
}

.middleContainer {
    display: flex;
    grid-gap: 50px;
    gap: 50px;
}

.inputLeft {
    width: calc((100% - 200px - 3rem) / 3);
    margin-top: 50px;
}

.inputLeft a {
    position: static;
}

.inputLeft input {
    border-radius: 5px;
    border: solid 3px black;
    padding: 0.5rem;
    font-size: 1rem;
    font-weight: 500;
    width: calc(150px - 1rem);
    margin-bottom: 1rem;
}

.table {
    margin: 50px 0;
}

.table h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
    width: 100px;
}

.table table {
    width: 100%;
}

.table th p {
    text-align: left;
}

.table th p {
    padding: 20px 10px;
}

.table td p {
    padding: 10px;
    word-break: break-all;
}

.table th,
.table td {
    width: calc(100% / 4);
}

.table .get {
    background-color: #ffc6ff;
}

.table .get:nth-child(odd) {
    background-color: #ffdaff;
}

.table .set {
    background-color: #fdffb6;
}

.table .set:nth-child(odd) {
    background-color: #fdffce;
}

.table .subtree {
    background-color: #9bf6ff;
}

.table .subtree:nth-child(odd) {
    background-color: #c3faff;
}

.table table {
    border-collapse: collapse;
}

.table .header {
    background-color: #fffcf4;
}

.iconButton {
    cursor: pointer;
}

.inputsNetwork {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.input select {
    border-radius: 5px;
    border: solid 3px black;
    padding: 0.4rem;
    font-size: 1rem;
    font-weight: 500;
}

table .true {
    background-color: #7bf1a8;
}

table .true:nth-child(odd) {
    background-color: #7bf1a8ab;
}

table .false {
    background-color: #ffef9f;
}

table .false:nth-child(odd) {
    background-color: #ffef9fa2;
}

.loader {
    text-align: center;
    vertical-align: middle;
    position: relative;
    display: flex;
    padding: 50px;
    justify-content: center;
}

.loader span {
    display: block;
    width: 20px;
    height: 20px;
    background: #ffef9f;
    border-radius: 50%;
    margin: 0 5px;
}

.loader span:nth-child(2) {
    background: #c1fba4;
}

.loader span:nth-child(3) {
    background: #7bf1a8;
}

.loader span:nth-child(4) {
    background: #90f1ef;
}

.loader span:not(:last-child) {
    -webkit-animation: animate 1.5s linear infinite;
            animation: animate 1.5s linear infinite;
}

@-webkit-keyframes animate {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(30px);
    }
}

@keyframes animate {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(30px);
    }
}

.loader span:last-child {
    -webkit-animation: jump 1.5s ease-in-out infinite;
            animation: jump 1.5s ease-in-out infinite;
}

@-webkit-keyframes jump {
    0% {
        transform: translate(0, 0);
    }
    10% {
        transform: translate(10px, -10px);
    }
    20% {
        transform: translate(20px, 10px);
    }
    30% {
        transform: translate(30px, -50px);
    }
    70% {
        transform: translate(-150px, -50px);
    }
    80% {
        transform: translate(-140px, 10px);
    }
    90% {
        transform: translate(-130px, -10px);
    }
    100% {
        transform: translate(-120px, 0);
    }
}

@keyframes jump {
    0% {
        transform: translate(0, 0);
    }
    10% {
        transform: translate(10px, -10px);
    }
    20% {
        transform: translate(20px, 10px);
    }
    30% {
        transform: translate(30px, -50px);
    }
    70% {
        transform: translate(-150px, -50px);
    }
    80% {
        transform: translate(-140px, 10px);
    }
    90% {
        transform: translate(-130px, -10px);
    }
    100% {
        transform: translate(-120px, 0);
    }
}

.timeout {
    flex-direction: column;
    width: -webkit-max-content;
